	module.exports = {"data":{"markdownRemark":{"html":"<p>Justin Ball is a software consultant and entrepreneur with a passion for Ruby.  He evolved from C++ and .Net monkey into a python programmer and finally found Ruby.  In the rare moments when he isn't writing code, talking about code or measuring his code productivity in profanity per hour, you can find him on his bike in the mountains or on the roads surrounding Cache Valley.  Justin can be found pontificating at <a href=\"http://www.justinball.com\">http://www.justinball.com</a>.</p>\n<p>Justin Ball is CTO of <a href=\"http://www.atomicjolt.com\">Atomic Jolt</a>, a software consulting company focused on software in education, startups and media. His experience includes most common development languages including C++, .Net, python, Javascript, Ruby and Elixir. Justin holds degrees in Electrical Engineering and Instructional Technology.</p>","frontmatter":{"path":"/bio","title":"Bio"}}},"pathContext":{}}

